import * as React from 'react';
import { Fragment, useState } from 'react';
import { useCreateCommunity } from '../src/assets/helper/index';
import Box from '../src/components/Box';
import { Wrapper } from '../src/components/Container';
import Home from '../src/models';

interface IProps {
    title: string;
    symbol: string
}

export default ({ title, symbol }: IProps) => {
    const [communityName, setcommunityName] = useState(title);
    const [communitySymbol, setcommunitySymbol] = useState(symbol);
    const [boarding, setOnboarding] = useState(1);

    if (boarding === 3) {
        useCreateCommunity({}, communityName, communitySymbol);
    }

    return (
        <Wrapper>
            <Home
                setOnboarding={setOnboarding}
                boarding={boarding}
                setcommunityName={setcommunityName}
                setcommunitySymbol={setcommunitySymbol}
            />
            {
                boarding === 3 &&
                (
                    <Fragment>
                        <Box title={`Community Name: ${communityName}`} >
                            <h3 style={{ color: 'black' }} >
                                Community Symbol: {communitySymbol}
                            </h3>
                        </Box>
                    </Fragment>
                )
            }
        </Wrapper>
    );
}